import { IMatch } from '../utils/types';

export const FIND_AND_REPLACE_TOGGLE = Symbol('FIND_AND_REPLACE_TOGGLE');
export const REPLACE_TEXT_AT_DECORATION = Symbol('REPLACE_TEXT_AT_DECORATION');
export const UPDATE_FIND_TEXT = Symbol('UPDATE_FIND_TEXT');
export const UPDATE_FIND_FRAGMENT = Symbol('UPDATE_FIND_FRAGMENT');
export const UPDATE_SEARCH_RESULT = Symbol('UPDATE_SEARCH_RESULT');

export function toggleFindAndReplace(visible?: boolean) {
  return {
    type: FIND_AND_REPLACE_TOGGLE,
    payload: { visible },
  };
}

export function replaceText(focus: IMatch, text: string) {
  return {
    type: REPLACE_TEXT_AT_DECORATION,
    payload: { focus, text },
  };
}


export function updateFindText(findText: string) {
  return {
    type: UPDATE_FIND_TEXT,
    payload: { findText },
  };
}

export function updateFindFragment(findFragment: string) {
  return {
    type: UPDATE_FIND_FRAGMENT,
    payload: { findFragment },
  };
}

export function updateSearchResult(focus: IMatch) {
  return {
    type: UPDATE_SEARCH_RESULT,
    payload: { focus },
  };
}
